.container{
    display: flex;
    justify-content: center;
    width: 70%;
    background-color: white;
    border: 5px solid black !important;
    border-radius: 10px !important;
    margin-bottom: 50px;
    padding: 20px 20px;
}

@media(max-width:770px){
    .container{
        width: 87%;
    }
}