body{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: rgb(231, 228, 228);
    font-family: 'Josefin Sans', sans-serif;
}

.container{
    align-items: center;
    display: flex;
    justify-content: center;
    flex-direction: column;
    font-family: 'Josefin Sans', sans-serif;
    background-color: rgb(3, 20, 20);
}

.header{
    /* width: 100%; */
    text-align: center;
    font-size: 50px;
    margin-top: -25px;
    margin-bottom: -40px;
    background-color: rgb(3, 20, 20);
    color: white;
    padding: 5px 0;
    text-decoration: underline;
}

.span{
    color: rgb(214, 3, 3);
}

.footer{
    text-align: center;
    color: white;
    background-color: rgb(3, 20, 20);
    padding: 10px 0;
    font-size: 22px;
}

@media(max-width:770px){
    .header{
        width: 100%;
    }
}